import { Pagination, FormLabel, Grid } from '@mui/material';

export const renderField = ({ label, renderControl, isCompact=false, required=false }: { label: string, renderControl: () => React.ReactNode, isCompact?: boolean, required?: boolean }) => {
    return (
        <>
            <Grid container>
                <Grid item flex={isCompact ? 1 : 1} id={`label-${label}`}>
                    <FormLabel sx={{ color: '#000', fontWeight: 'bold' }} required={required}>{label}</FormLabel>
                </Grid>
                <Grid item flex={isCompact ? 6 : 4}>
                    {renderControl()}
                </Grid>
            </Grid>

            <br />
            <br />
        </>
    )
}

export const renderPagination = ({ router, totalCount, page, extraQueryParams={} }) => {
    const handlePageChange = (event, value) => {
        router.push({
            query: { page: value, ...extraQueryParams },
        });
    };

    return (
        <Pagination
            count={Math.ceil(totalCount / 10)}
            page={parseInt(page, 10)}
            onChange={handlePageChange}
            color="primary"
            sx={{ marginTop: '20px', display: 'flex', justifyContent: 'center' }}
        />
    )
}

const capitalize = (string) => {
    if (!string) return '';
    return string.charAt(0).toUpperCase() + string.slice(1).toLowerCase();
}

export const renderJobStatus = (job) => {
    let backgroundColor = 'inherit';
    let color = '#fff';

    if (job.status === 'active') {
        backgroundColor = '#4a6cbd';
    }

    if (job.status === 'lead') {
        backgroundColor = 'rgb(251, 249, 185)';
        color = '#000';
    }

    if (['onHold', 'canceled', 'closed'].includes(job.status)) {
        backgroundColor = 'rgb(200, 105, 80)';
    }

    if (job.status === 'filled') {
        backgroundColor = 'rgb(221, 160, 221)';
    }

    return <span style={{ borderRadius: '5px', backgroundColor, color, width: '90px', padding: '2px 4px' }}>{capitalize(job.status)}</span>
}

export const renderClientStatus = (client) => {
    let backgroundColor = '#FA8072';
    let color = '#fff';

    if (!client.status) {
        client.status = 'active';
    }

    if (client.status === 'active') {
        backgroundColor = '#4a6cbd';
    }

    return <span style={{ borderRadius: '5px', backgroundColor, color, width: '90px', padding: '2px 4px' }}>{capitalize(client.status)}</span>
}

export const renderDetailItem = (label: string, value) => {
    return (
        <>
            <Grid container>
                <Grid item flex={3} id={`label-${label}`}>
                    {label}
                </Grid>
                <Grid item flex={4}>
                    {value}
                </Grid>
            </Grid>

            <br />
        </>
    )
}