import React, { useRef, ReactNode } from 'react';
import type { ComponentType } from 'react';
import dynamic from 'next/dynamic';
import 'react-quill/dist/quill.snow.css';
import type ReactQuill from 'react-quill';

type Props = {
  renderExtraContent?: (quillRef: React.RefObject<ReactQuill>) => ReactNode;
} & React.ComponentProps<typeof ReactQuill>;

const QuillWrapper = dynamic(
  async () => {
    const { default: RQ } = await import('react-quill');

    const Quill = RQ.Quill;

    const Embed = Quill.import('blots/block/embed');
    class Divider extends Embed {
      // if you want to add inline style, override create method
      static create(value: any) {
        const node = super.create(value);
        return node;
      }
      // set blotName & tagName
      static blotName = 'divider';
      static tagName = 'hr';
    }

    Quill.register({ 'formats/hr': Divider });

    // eslint-disable-next-line react/display-name
    return ({ ...props }) => {
      const quillRef = useRef(null); 

      const { renderExtraContent } = props;

      props.modules = {
        toolbar: [
          [{ header: [1, 2, false] }],
          ['bold', 'italic', 'underline', 'strike'],
          ['blockquote', 'code-block'],
          [{ list: 'ordered' }, { list: 'bullet' }],
          ['link', 'image'],
          ['clean'],
        ],
      };

      props.formats = [
        'header',
        'bold',
        'italic',
        'underline',
        'strike',
        'blockquote',
        'code-block',
        'list',
        'bullet',
        'link',
        'image',
        'hr',
        'divider',
      ];

      return (
        <>
          <RQ ref={quillRef} {...props} />
          {renderExtraContent && renderExtraContent(quillRef)}
        </>
      )
    };
  },
  {
    ssr: false,
  },
) as ComponentType<Props>;

export default QuillWrapper;
