import React, { useState } from 'react';
import EditIcon from '@mui/icons-material/Edit';
import CancelIcon from '@mui/icons-material/Cancel';
import CheckIcon from '@mui/icons-material/Check';
import { Grid } from '@mui/material';

export const fcSx = {
    '& .MuiSelect-select': {
        width: '200px',
        padding: '8px', // Adjust padding as needed
    },
    '& .MuiOutlinedInput-input': {
        width: '200px',
        padding: '8px', // Adjust padding inside the input
    },
    '& .MuiFormLabel-root': {
        top: '-10px'
    },
}

const DetailItem = ({ label, fieldName, value, onSave, editControl }) => {
    const [editingSimpleField, setEditingSimpleField] = useState('');
    const [hoveringField, setHoveringField] = useState('');

    return (
        <>
            <Grid container>
                <Grid
                    item
                    flex={3}
                    id={`label-${label}`}
                    sx={{
                        position: 'relative'
                    }}
                    onMouseOver={() => {
                        setHoveringField(fieldName);
                    }}
                    onMouseOut={() => {
                        setHoveringField('');
                    }}
                >
                    <span>{label}</span>

                    {
                        <div style={{ position: 'absolute', top: '0px', left: '140px' }}>
                            {editingSimpleField === fieldName ?
                                <>

                                    <CheckIcon
                                        sx={{ cursor: 'pointer', height: '20px', color: 'green', fontWeight: 'bold' }}
                                        onClick={() => {
                                            onSave().then(() => {
                                                setEditingSimpleField('');
                                            })
                                        }}
                                    />

                                    <CancelIcon
                                        sx={{ cursor: 'pointer', height: '20px' }}
                                        onClick={() => {
                                            setEditingSimpleField(null);
                                        }}
                                    />
                                </>
                                : hoveringField === fieldName && (
                                    <EditIcon
                                        sx={{ cursor: 'pointer', height: '20px' }}
                                        onClick={() => {
                                            setEditingSimpleField(fieldName);
                                        }}
                                    />
                                )}
                        </div>
                    }
                </Grid>

                <Grid item flex={4}>
                    {
                        editingSimpleField === fieldName ? editControl() : value
                    }
                </Grid>

            </Grid>

            <br />
        </>
    )
};

export default DetailItem;
