
import React, { useState } from 'react';
import moment from 'moment';
import { FileIcon, defaultStyles } from 'react-file-icon';
import { readFile } from '../../lib/api/public';
import confirm from '../../lib/confirm';
import { Box, Dialog, DialogContent, DialogTitle, IconButton, Grid } from '@mui/material';
import { Paper, TableContainer, Table, TableCell, TableBody, TableRow } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import DeleteButton from 'components/common/DeleteButton';

type File = {
  name: string;
  url: string;
  date: Date;
}

type Props = {
    title?: string;
    files: File[];
    rightSide?: React.ReactNode;
    onDelete: (file: File) => void;
};

const Files = ({ title = 'Attachments', files, rightSide, onDelete }: Props) => {
    const [selectedAttachment, setSelectedAttachment] = useState(null);

    const preview = (attachment) => {
        setSelectedAttachment(attachment);
    }

    const onClose = () => {
        setSelectedAttachment(null);
    }

    const isImage = (extension) => {
        return ['jpeg', 'jpg', 'png'].includes(extension);
    }

    const renderPopup = () => {
        if (!selectedAttachment) {
            return null;
        }

        return (
            <Dialog onClose={onClose} open fullScreen style={{ width: '50%', left: 'inherit' }}>
                <DialogTitle>
                    <Box display="flex" alignItems="center">
                        <Box flexGrow={1}>Preview</Box>
                        <Box>
                            <IconButton onClick={onClose}>
                                <CloseIcon />
                            </IconButton>
                        </Box>
                    </Box>
                </DialogTitle>


                <DialogContent>
                    <iframe
                        src={readFile(selectedAttachment)}
                        width="100%"
                        height="100%"
                        style={{ border: 'none', outline: 'none' }}
                    />
                </DialogContent>
            </Dialog>
        )
    }

    return (
        <div>
            <Grid container justifyContent="space-between" alignItems="center">
              <Grid item><h3>{title}</h3></Grid>
              <Grid item>
                {rightSide}
              </Grid>
            </Grid>

            {renderPopup()}

            <br />

              <TableContainer component={Paper}>
                  <Table>
                    <TableBody>
                        {(files || []).map((attachment, index) => {
                            const extension = attachment.name.split('.').pop();

                            return (
                                <TableRow key={index}>
                                    <TableCell onClick={preview.bind(this, attachment.url)}>
                                        {isImage(extension) ? 
                                        <img src={readFile(attachment.url)} height={30} width={30} /> :

                                        <div style={{ width: '30px', cursor: 'pointer' }}>
                                            <FileIcon
                                                extension={extension}
                                                {...defaultStyles[extension] || {}}
                                            />
                                        </div>
                                        }
                                    </TableCell>

                                    <TableCell>
                                        <a href={readFile(attachment.url)}>{attachment.name}</a>
                                    </TableCell>

                                    <TableCell>
                                        {moment(attachment.date).format('MM/DD/YYYY')}
                                    </TableCell>

                                    <TableCell>
                                        <DeleteButton
                                            onClick={() => {
                                                confirm({
                                                    title: 'Confirmation',
                                                    message: 'Are you sure to delete ?',
                                                    onAnswer: (answer) => {
                                                        if (answer) {
                                                            onDelete(attachment)
                                                        }
                                                    }
                                                })
                                            }}
                                        />
                                    </TableCell>
                                </TableRow>
                            )
                        })}
                    </TableBody>
                </Table>

                {
                    !files || files.length === 0 && (
                        <Box sx={{ textAlign: 'center' }}>
                            <p>No files</p>
                        </Box>
                    )
                }
            </TableContainer>
        </div>
    )
};

export default Files;