import React, { useState } from 'react';
import { readFile, uploadFileRequest } from '../../lib/api/public';
import CircularProgress from '@mui/material/CircularProgress';
import { Table } from '@mui/material';
import AttachFileIcon from '@mui/icons-material/AttachFile';
import DeleteButton from './DeleteButton';

type File = {
  name?: string;
  url: string;
  date?: Date;
}

type Props = {
    label: string;
    onChange: (results: any) => void;
    multiple?;
    showList?: boolean;
    defaultFiles?: File[];
};

const Uploader = ({ label, onChange, multiple=true, showList=true, defaultFiles }: Props) => {
    const [isUploading, setUploading] = useState(false);
    const [results, setResults] = useState(defaultFiles || []);

    const uploadFile = (e) => {
        const files = e.target.files;

        for (const file of files) {
            const formData = new FormData();
            formData.append('file', file);

            setUploading(true);

            uploadFileRequest(formData)
                .then((response) => {
                    setUploading(false);

                    response
                        .json()
                        .then(({ result }) => {
                            results.push({
                                url: result,
                                date: new Date(),
                                name: file.name
                            });

                            setResults([...results]);

                            if (files.length === results.length) {
                                if (multiple) {
                                    onChange(results);
                                } else {
                                    onChange(results[0]);
                                }
                            }
                        })
                        .catch((error) => {
                            console.error(error.message);
                        });
                })
                .catch((error) => {
                    setUploading(false);
                    console.error(error.message);
                });
        }
    }

    const removeResult = (result: File) => {
        const newResults = results.filter(r => r.url !== result.url);

        setResults(newResults);

        if (multiple) {
            onChange(newResults);
        } else {
            onChange(newResults[0]);
        }
    }

    const renderList = () => {
        if (!showList) {
            return null;
        }

        return (
            <>
                {results.length > 0 ? (<><br /><br /></>) : null}

                <Table>
                    {results.map((result) => {
                        return (
                            <div style={{ display: 'flex' }}>
                                <DeleteButton onClick={() => removeResult(result)} />
                                <a href={readFile(result.url)} target='__blank'>{result.name}</a>
                            </div>
                        );
                    })}
                </Table>
            </>
        )
    }

    return (
        <div style={{ position: 'relative', display: 'inline-block' }}>
            <input type='file' id={`uploader-${label}`} onChange={uploadFile} style={{ display: 'none' }} multiple={multiple} />

            <label
                htmlFor={`uploader-${label}`}
                style={{
                    padding: '10px 35px',
                    borderRadius: '5px',
                    width: '215px !important',
                    cursor: 'pointer',
                    background: '#ebecf0',
                    color: '#4f4f51',
                    border: '1px solid #dfe0e4',
                    position: 'relative',
                }}
            >

                <div
                    style={{
                        position: 'absolute',
                        height: '20px',
                        left: '5px',
                        top: '10px'
                    }}
                >
                 {isUploading ? <CircularProgress size={20} /> : <AttachFileIcon />}
                </div>

                {label}
            </label>

            {renderList()}
        </div>
    )
};

export default Uploader;